<template>
    <div>
        <!-- 將 ContentResume 包裹在一個 div 並設置 ref -->
        <div class="ContentResume" ref="contentResume">
            <ContentResume />
        </div>
        <!-- 傳遞 contentResume 的 DOM 元素引用給 ResumeDownload -->
        <div class="resume-download-section">
            <ResumeDownload :resumeRef="resumeContentRef" />
        </div>
    </div>
</template>

<script>
import ContentResume from './ContentResume.vue';
import ResumeDownload from './ResumeDownload.vue';

export default {
    components: {
        ContentResume,
        ResumeDownload
    },
    data() {
        return {
            resumeContentRef: null // 用來存放 ContentResume 的 DOM 元素
        };
    },
    mounted() {
        // 在 DOM 完全加載後設置 resumeContentRef
        this.resumeContentRef = this.$refs.contentResume;

        // 檢查 $refs.contentResume 是否被正確設置
        if (!this.resumeContentRef) {
            console.error("ContentResume DOM element not found.");
        } else {
            console.log("ContentResume DOM element successfully set.");
        }
    }
};
</script>

<style scoped>
/* 使 ResumeDownload 部分置於底部 */
.resume-download-section {
    text-align: center;
    /* 讓按鈕居中 */
}
</style>