<template>
    <div>
        <!-- 這是下載按鈕 -->
        <button @click="downloadPdf" class="download-button">Download Resume.pdf</button>
    </div>
</template>

<script>
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
// import ContentResume from './ContentResume.vue';

export default {
    // components: {
    //     ContentResume
    // },
    props: ['resumeRef'], // 從父組件接收 resumeRef

    methods: {
        downloadPdf() {
            // 使用從父組件傳遞進來的 resumeRef
            const resumeElement = this.resumeRef;

            if (resumeElement instanceof HTMLElement) {
                // 使用 html2canvas 將元素轉換為圖像
                html2canvas(resumeElement).then(canvas => {
                    const imgData = canvas.toDataURL('image/png');

                    // 創建 jsPDF 實例，設置為 A4 大小，單位為 'mm'
                    const pdf = new jsPDF({
                        orientation: 'portrait',
                        unit: 'mm',
                        format: 'a4'
                    });

                    // 將圖像放入 PDF 中
                    const imgWidth = 210; // A4 寬度 210mm
                    // const pageHeight = 297; // A4 高度 297mm
                    const imgHeight = (canvas.height * imgWidth) / canvas.width; // 按比例調整圖像高度

                    let position = 0;
                    pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);

                    // 下載 PDF 文件
                    pdf.save('resume.pdf');
                });
            } else {
                console.error("Invalid DOM element passed as resumeRef.");
            }
        }
    }
};
</script>

