    /* 調整字體大小 */
    padding: 15px 30px;
    /* 增加按鈕內邊距，讓按鈕更大 */
    background-color: #000;
    /* 按鈕背景色 */
    color: white;
    /* 按鈕文字顏色 */
    border: none;
    /* 去除邊框 */
    border-radius: 5px;
    /* 圓角邊框 */
    cursor: pointer;
    /* 指針變成手形 */
    transition: background 0.5s ease, box-shadow 1s ease;
    /* 添加平滑過渡效果 */
    /* 添加過渡效果 */
}

